import Vue from 'vue'
import App from './ui/App.vue'

const MandalaUi = ((container, effects = [], createFn, eraseFn) => {
  window.__ui = new Vue({
    el: createInContainer(container),
    render: (h) => h(App),
    data: () => ({
      effects: [],
      templates: null,
      headIdx: 0,
    }),
    created() {
      Promise.all(effects).then((json) =>
        this.templates = json.map((j) => this.templateEffect(j)))
    },
    methods: {
      templateEffect(json) {
        this.effects.push(json.nodes.map((n) => ({
          name: json.name,
          polyR: json.gon[n.gon].radius,
          polyN: json.gon[n.gon].vertCount,
          vertR: json.vert[n.vert].radius,
          debug: json.debug,
        })))
      },
    },
    template: `
    <div id="mandala-ui">
    <div class="legend">
      <div class="shape-radius">Shape radius</div>
      <div class="vert-count">Vert count</div>
      <div class="vert-radius">Vert radius</div>
    </div>
    <div class="mdla-item" v-for="(m, i) in effects">
      <div class="header">
        Mandala {{i}}: {{m.name}}
        <span class="debug-toggle">
          <input type="checkbox" title="Debug mode" v-model="m.debug"/>
        </span>
      </div>
      <div :class="'mdla-nodes level level_' + i" v-for="(n, i) in m">
        <div class="input shape-radius"><input type="number" min="1" :title="n.polyR + ' radius'" v-model.number="n.polyR"></input></div>
        <div class="input vert-count"><input type="number" min="1" :title="n.polyN + ' vertices'" v-model.number="n.polyN"></input></div>
        <div class="input vert-radius"><input type="number" min="1" :title="n.vertR + ' radius'" v-model.number="n.vertR"></input></div>
      </div>
      <div :class="'mdla-nodes new-node level_' + m.length" @click="newLevel(i)">
        <span class="center">+</span>
      </div>
      <div class="copy-mandala">
        <label>Copy</label>
        <input type="text" :value="JSON.stringify(m)" v-model="copiedMandala"></input></div>
      </div>
    </div>`,
  })
})

function createInContainer(container) {
  const id = 'mandala-ui'
  let el = document.createElement('div')
  el.setAttribute('id', id)
  document.body.appendChild(el)
  return '#' + id
}

export default MandalaUi


